// Interfaces
enum Color {
  rojo = 'Rojo',
  Verde = 'Verde'
}
interface Rectangulo {
  ancho: number
  alto: number
  color: Color
}

let rect: Rectangulo = {
  ancho: 4,
  alto: 6,
  color : Color.rojo
}

function area(r: Rectangulo): number {
   return r.alto * r.ancho
}
const areaRect = area(rect)
console.log(areaRect);

console.log(rect.toString())

rect.toString = function(){
  return this.color
  ? `un rectangulo de color ${this.color}`
  : `Un rectangulo`
}

console.log(rect.toString())